body {
  margin: 0;
  padding: 0;
}

/* Navigation.css */

.navigation {
    flex-basis: 20%;
    min-width: 300px;
    padding: 20px;
  }
/* Selection.css */

.selection {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
  }
  
  .selection label {
    font-size: 1rem;
    margin-bottom: 10px;
    color: #555;
  }
  
  .selection select {
    max-width: 150px;
  }
/* Slider.css */

.slider {
    margin-bottom: 30px;
  }
  
  .slider label {
    color: #555;
    font-size: 1rem;
    margin-bottom: 20px;
    display: block;
    text-transform: capitalize;
  }
  
  .input-range__slider {
    background: #E91E63;
    border: none;
  }
  
  .input-range__track {
    background: #ddd;
  }
  
  .input-range__track--active {
    background: #E91E63;
  }
  
  .input-range__label--value .input-range__label-container {
    background: #E91E63;
    color: white;
    font-size: 0.65rem;
    padding: 2px 5px;
    border-radius: 2px;
  }
  
  .input-range__label--min .input-range__label-container,
  .input-range__label--max .input-range__label-container
   {
    font-size: 0.65rem;
    color: #888;
    left: 0;
  }
  
  .input-range__label--max .input-range__label-container {
    left: 25%;
  }
  
.sc-chat-window {
  width: 100%;
  height: 200px;
  height: calc(100% - 120px);
  /* position: fixed;
    right: 25px;
    bottom: 100px; */
  box-sizing: border-box;
  box-shadow: 0px 7px 40px 2px rgba(148, 149, 150, 0.3);
  background: white;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  -webkit-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
  border-radius: 10px;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}

.sc-chat-window.closed {
  opacity: 0;
  visibility: hidden;
  bottom: 90px;
}

.sc-message-list {
  max-height: 320px;
  height: 80%;
  overflow-y: scroll;
  background-color: white;
  background-size: 100%;
  padding: 40px 0px;
  display: flex;
  flex-direction: column-reverse;
}

.sc-message--me {
  text-align: right;
}
.sc-message--them {
  text-align: left;
}

/* messages */
.sc-message {
  width: 300px;
  margin: auto;
  padding-bottom: 10px;
  display: flex;
}

.sc-message--content {
  width: 100%;
  display: flex;
}

.sc-message--content.sent {
  justify-content: flex-end;
}

.sc-message--content.sent .sc-message--avatar {
  display: none;
}

.sc-message--avatar {
  background-image: url(https://d13yacurqjgara.cloudfront.net/assets/avatar-default-aa2eab7684294781f93bc99ad394a0eb3249c5768c21390163c9f55ea8ef83a4.gif);
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center;
  min-width: 30px;
  min-height: 30px;
  border-radius: 50%;
  align-self: center;
  margin-right: 15px;
}

.sc-message--text {
  padding: 17px 20px;
  border-radius: 6px;
  font-weight: 300;
  font-size: 14px;
  line-height: 1.4;
  white-space: pre-wrap;
  -webkit-font-smoothing: subpixel-antialiased;
  word-wrap: break-word;
  width: calc(100% - 90px);
}

.sc-message--content.sent .sc-message--text {
  color: white;
  background-color: #4e8cff;
  max-width: calc(100% - 120px);
  word-wrap: break-word;
}

.sc-message--content.received .sc-message--text {
  color: #263238;
  background-color: #f4f7f9;
  margin-right: 40px;
}

/* user input */
.sc-user-input {
  min-height: 55px;
  margin: 0px;
  position: relative;
  bottom: 0;
  display: flex;
  background-color: #f4f7f9;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  -webkit-transition: background-color 0.2s ease, box-shadow 0.2s ease;
  transition: background-color 0.2s ease, box-shadow 0.2s ease;
}

.sc-user-input--text {
  width: 300px;
  resize: none;
  border: none;
  outline: none;
  border-bottom-left-radius: 10px;
  box-sizing: border-box;
  padding: 18px;
  font-size: 15px;
  font-weight: 400;
  line-height: 1.33;
  white-space: pre-wrap;
  word-wrap: break-word;
  color: #565867;
  -webkit-font-smoothing: antialiased;
  max-height: 200px;
  overflow: scroll;
  bottom: 0;
  overflow-x: hidden;
  overflow-y: auto;
}

.sc-user-input--text:empty:before {
  content: attr(placeholder);
  display: block; /* For Firefox */
  color: rgba(86, 88, 103, 0.3);
  outline: none;
}

/* header */
.sc-header {
  background: #4e8cff;
  min-height: 75px;
  border-top-left-radius: 9px;
  border-top-right-radius: 9px;
  color: white;
  padding: 10px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
  position: relative;
  box-sizing: border-box;
  display: flex;
}

.sc-header--img {
  border-radius: 50%;
  align-self: center;
  padding: 10px;
}

.sc-header--team-name {
  align-self: center;
  padding: 10px;
  flex: 1 1;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  border-radius: 5px;
}

.sc-header--close-button {
  width: 40px;
  align-self: center;
  height: 40px;
  margin-right: 10px;
  box-sizing: border-box;
  cursor: pointer;
  border-radius: 5px;
}

.sc-header--close-button:hover {
  background: #4882ed;
}

.sc-header--close-button img {
  width: 100%;
  height: 100%;
  padding: 13px;
  box-sizing: border-box;
}

.row-header {
  margin: 0px auto;
  padding: 0px auto;
}

.row-content {
  margin: 0px auto;
  padding: 50px 0px 50px 0px;
  border-bottom: 1px ridge;
  min-height: 400px;
}

.interactions {
  display: flex;
  justify-content: space-between;
  overflow-y: scroll;
  padding: 15px;
}

.footer {
  background: linear-gradient(262deg, #ff7854, #fd267d);
  margin: 0px auto;
  padding: 20px 0px 20px 0px;
}
.jumbotron {
  padding: 70px 30px 70px 30px;
  margin: 0px auto;
  /* background: #EA80FC ; */
  color: floralwhite;
  background: linear-gradient(262deg, #ff7854, #fd267d);
}

address {
  font-size: 80%;
  margin: 0px;
  color: #0f0f0f;
}

.navbar-dark {
  /* background-color: #9C27B0; */
  background: linear-gradient(262deg, #ff7854, #fd267d);
}

.card {
  line-height: 14px;
  white-space: nowrap;
  margin: 10px;
  padding: 10px;
  border: 2px solid #e3e3e3;
  box-shadow: rgba(0, 0, 0, 0.2) 0 2px 10px;
  border-radius: 4px;
  overflow: hidden;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  opacity: 0.8;
}
.interactions {
  display: flex;
  justify-content: space-between;
  overflow-y: scroll;
  padding: 15px;
}
.custom-tag {                                
    background: linear-gradient(262deg, #ff7854, #fd267d);
    border: 0;
}            

.cardImage {
  width: 200px;
}

.cardName {
  height: 60px;
  width: 200px;
  padding: 8px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.search-container {
  text-align: left;
}
.select-container {
  text-align: right;
}
